@import '../../variables';
@import '../../mixins';

.content {
  //background: pink;
  width: 85%;
  max-width: 1200px;
  padding: 5.8em 0;
  margin: auto;
  //margin: 2em auto;
}

// Section One
.sectionOne {
  background: linear-gradient(94.63deg, #003849 0%, rgba(50, 0, 73, 0.84) 100%);
  border-radius: 28px;
  margin: 4em 0 2em 0;
  @include flexRegular(row, space-between);
  padding: 3em 5em;
  position: relative;
  //background: pink;
}

.oneLeft {
  color: #fff;

  h1 {
    font-family: 'Neue-bold';
    width: 60%;
    font-size: 2.6em;
    line-height: 45px;
  }

  p {
    margin: 1em 0;
    font-family: 'Raleway', sans-serif;
    width: 45%;
    line-height: 25px;
    font-size: 1.08em;
  }
}

.oneRight {
  img {
    position: absolute;
    top: 0;
    right: 0;
    width: 39%;
    max-width: 582.77px;
    max-height: 509px;
  }
}

@media (min-width: 1450px) {
  .oneRight {
    img {
      position: absolute;
      // top: 1em;
      // right: 1em;
      // width: 35%;
      top: 0;
      right: 0;
      width: 35%;
      max-width: 582.77px;
      max-height: 509px;
    }
  }
}

// Section Two
.sectionTwo {
  margin: 7em 0 2em 0;

  h1 {
    font-family: 'Neue-bold';
    font-size: 2.8em;
    text-align: center;
    margin-bottom: 0.5em;
  }
}

.sTwoContent {
  background: linear-gradient(94.63deg, #003849 0%, rgba(50, 0, 73, 0.84) 100%);
  border-radius: 28px;
  @include flexRegular(row, space-between);
  padding: 3em 5em;

  .twoRight {
    color: #fff;
    width: 50%;

    //background: pink;
    h2 {
      font-family: 'Oxygen', sans-serif;
      width: 55%;
      font-size: 2em;
    }

    p {
      margin: 1em 0;
      font-family: 'Raleway', sans-serif;
      width: 95%;
      line-height: 25px;
      font-size: 1.1em;
    }

    span {
      color: $yellowMain;
    }
  }

  .twoBtns {
    @include flexRegular(row, flex-start);
    margin-top: 4em;

    .stake {
      @include buttonRegular($yellowMain, 180px, 40px);
      @include flexRegular(row, center);

      img {
        margin-left: 0.5em;
      }
    }

    .buyAfen {
      @include buttonRegular(none, 180px, 40px);
      @include flexRegular(row, center);
      border: 1px solid #ffcc00;
      color: #fff;
      margin-right: 2em;

      img {
        margin-left: 0.5em;
      }
    }
  }

  .twoLeft {
    img {
      width: 90%;
    }
  }
}

// Section Three
.sectionThree {
  margin: 4em 0 2em 0;
}

.sThreeContent {
  background: linear-gradient(94.63deg, #003849 0%, rgba(50, 0, 73, 0.84) 100%);
  border-radius: 28px;

  @include flexRegular(row, space-between);
  padding: 3em 5em;

  .threeLeft {
    color: #fff;
    width: 45%;

    h2 {
      font-family: 'Neue-bold';
      width: 85%;
      font-size: 2em;
    }

    p {
      margin: 1em 0;
      font-family: 'Raleway', sans-serif;
      width: 95%;
      line-height: 25px;
      font-size: 1.1em;
    }

    span {
      color: $yellowMain;
    }

    button {
      margin-top: 2.5em;
      @include buttonRegular($yellowMain, 220px, 40px);
      @include flexRegular(row, center);

      img {
        margin-left: 0.5em;
      }
    }
  }
}

