// ----- Team
.trending {
  padding: 4em 0 11em 0;
}
.trTop {
  h1 {
    font-family: Neue-bold;
    text-align: center;
    font-size: 2.2em;
  }
  margin-bottom: 4em;
}
.trBody {
  width: 100vw;
  // background: lavender;

  overflow: hidden;
  position: relative;
}
.trSlides1 {
  margin-top: 1em;
  animation: scrolling3 10s linear infinite;
  @include flexRegular(row, flex-start);
  cursor: pointer;
  //height: 100%;
  &:hover {
    animation-play-state: paused;
  }
}

.trSlides2 {
  margin-top: 6em;
  animation: scrolling3 15s reverse infinite;
  //animation-direction: alternate;
  @include flexRegular(row, flex-start);
  cursor: pointer;
  height: 100%;
  &:hover {
    animation-play-state: paused;
  }
  //background: pink;
}

@keyframes scrolling3 {
  0% {
    transform: translateX(0vw);
  }
  100% {
    transform: translateX(-150vw);
  }
}
@keyframes scrolling4 {
  0% {
    transform: translateX(0vw);
  }
  100% {
    transform: translateX(calc(1 * (150vw)));
  }
}
.trSlide {
  @include flexRegular(row, center);
  width: 26vw;
  flex-shrink: 0;
  margin-left: 4vw;
  //background: pink;
}

.trSlidesp {
  width: 15vw;
}
